* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.is-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  align-items: center;
  display: flex;
}

.page-header {
  width: 100%;
  height: 70px;
  background-image: url("bg.57187ac5.jpg");
  background-position: center;
  background-repeat: repeat;
  background-size: contain;
  justify-content: center;
  display: flex;
  position: relative;
}

button {
  text-align: center;
  cursor: pointer;
  color: inherit;
  border: 2px;
  font-family: inherit;
  display: inline-block;
}

.search-form {
  text-align: center;
}

.search-form__input {
  width: 200px;
  height: 50px;
  font-family: cursive;
  font-size: 18px;
  font-weight: 500;
}

.search-form__button {
  width: 50px;
  height: 50px;
  color: #000;
  background-color: #f5f5f5;
}

.search-form__button:hover, .search-form__button:focus {
  color: #9932cc;
}

.search-logo {
  fill: currentColor;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

a {
  color: inherit;
  text-decoration: none;
}

.gallery {
  max-width: 1400px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 20px auto;
  display: flex;
}

.gallery img {
  height: 75%;
  object-fit: cover;
  object-position: center;
}

.gallery a {
  min-width: 300px;
  flex-basis: calc(25% - 11.25px);
}

.photo-card {
  height: 300px;
  border: 1px solid gray;
  border-radius: 5px;
  flex-direction: column;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  overflow: hidden;
}

.photo-card:hover {
  transform: scale(1.03);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.info {
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  gap: 12px;
  padding: 8px 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  gap: 5px;
  font-family: cursive;
  display: flex;
}

.load-more {
  max-width: 250px;
  text-align: center;
  color: #000;
  background-color: #f5f5f5;
  border: 1px solid #9932cc;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  font-family: cursive;
  font-size: 20px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.load-more:hover {
  color: #9932cc;
  border-color: rgba(0, 0, 0, .12);
  transform: scale(1.03);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.end-message {
  width: 100%;
  text-align: center;
  color: #9932cc;
  font-family: cursive;
  font-size: 28px;
  font-weight: 700;
}

.change-page-button {
  color: #f5f5f5;
  width: 120px;
  height: 60px;
  border: 1px solid #f5f5f5;
  border-radius: 4px;
  font-family: cursive;
  position: absolute;
  top: 5px;
  left: 20px;
}

